import NetInfo from '@react-native-community/netinfo';
import type { StoredToken } from '@entur/react-native-traveller';

export const verifyCorrectTokenId = (
  initialTokenId: string,
  tokenId: string
) => {
  if (tokenId !== initialTokenId) {
    throw Error(
      `Activated token ${tokenId} does not match initial token id ${initialTokenId}`
    );
  }
};

const STORAGE_KEY_PREFIX = '@mobiletokensdk-state';
export const getStoreKey = (accountId: string) =>
  `${STORAGE_KEY_PREFIX}#${accountId}`;

export const missingNetConnection = () =>
  NetInfo.fetch().then((state) => {
    return !state.isConnected ?? true;
  });

export const isTokenInspectable = (tokens: StoredToken[], tokenId: string) =>
  tokens
    .find((t) => t.id === tokenId)
    ?.allowedActions.includes('TOKEN_ACTION_TICKET_INSPECTION') || false;
